import React from "react";
import '../PontosDeLuz/PontosDeLuz.css';
import InformationPopup from '../../InformationPopup/InformationPopup';
import { useNavigate } from "react-router-dom";

const PontosDeLuz = () => {

    const luminariasText = (
    <>
        Caso voc utilize <strong>luminrias direcionais</strong> (spots de sobrepor ou de embutir) para a luz geral, Lumos recomenda a troca por luminrias difusas ou indiretas (caso seu teto seja claro). 
        Isso vai ajudar a luz ser distribuda uniformemente e seu cmodo vai ter uma iluminao geral mais eficiente em termos de conforto lumnico. 
        As luminrias que permitem maior difuso do fluxo luminoso so aquelas com globo todo de vidro, podendo ser de sobrepor ou pendente, ou com vidro difusor no caso de luminria de embutir (teto com forro). 
        As <strong>luminrias indireta</strong> mais eficientes so aqueles pendentes que a luz  direcionada para cima, na direo do teto.
        Quando o teto  claro, a luz que incide nele  refletida para o ambiente de forma difusa.
    </>
    );
    const luminariasTitle = "Luminrias";

    const openPopupLuminarias = () => {
        document.getElementById('popup-luminarias').style.visibility = 'visible';
        document.getElementById('popup-blackout').style.visibility = 'visible';
    }

    const sancasText = (
    <>
        <strong>Sancas centrais:</strong> muito usadas para fornecer a luz geral de um ambiente e esse pode ser o seu caso! 
        Elas conduzem a luz para o teto claro e a luz que incide nele  refletida para o ambiente de forma difusa, assim como as luminrias indiretas.  
        Se voc tem certeza de que sua sanca no produz somente uma luz de efeito e  ela a responsvel pela luz geral do seu cmodo, com medidas corretas para a luz que sai dela iluminar uniformemente o ambiente, voc pode utilizar o Lumos!
        <br />
        <br />
        <strong>Sancas com lmpadas lineares (fluorescente ou leds):</strong> preencha o light macth com 1 ponto de luz e a quantidade de lmpadas que fica na sua sanca. 
        O Lumos vai informar as unidades fotomtricas que cada lmpada deve ter.
        <br />
        <br />
        <strong>Sancas com fita de leds:</strong> preencha o light macth com 1 ponto de luz e 1 lmpada. O Lumos vai informar o fluxo luminoso total que deve sair da sanca. 
        Divida esse valor pelo permetro da sanca e voc vai encontra a quantidade de Lmens (Lm) por metro linear (ml) que sua sanca deve ter. 
        Fitas de Led so especificadas pela informao Lm/ml.
    </>)
    ;
    const sancasTitle = "Sancas";

    const openPopupSancas = () => {
        document.getElementById('popup-sancas').style.visibility = 'visible';
        document.getElementById('popup-blackout').style.visibility = 'visible';
    }

    const niText = (
        <>
            Por vezes, a iluminao de um ambiente se d por vrios pontos direcionais que destacam superfcies,  o que chamamos de luz de destaque. 
            Isso  muito comum em ambientes residenciais, onde as atividades no so, em grande parte, de muita preciso. 
            Nesse caso, Lumos no  para voc! No se preocupe, esse tipo de iluminao, normalmente  realizada com um projeto de luminotcnica e o profissional que voc contratou saber orientar voc na escolha correta de seus equipamentos. 
            Contrate sempre um especialista em Light Design (Designer de Interior, Arquiteto, Engenheiro).
        </>
    );
    const niTitle = "No consegui identificar os pontos de luz geral";

    const openPopupNaoId = () => {
        document.getElementById('popup-nao-id').style.visibility = 'visible';
        document.getElementById('popup-blackout').style.visibility = 'visible';
    }

    const navigate = useNavigate();
    const handleClose = () => {
        navigate(-1);
    };

    return (
        <div className="pontos-de-luz">
            <div className="pdl-title-container">
                <div className="pdl-title">
                    <p className="pdl-title-text">
                       Pontos de Luz
                    </p>
                </div>

                <div
                    onClick={handleClose} 
                    className="pdl-button">
                    <p></p>
                </div>
            </div>

            <div className="pdl-text-container">
                <p className="pdl-text">
                    Os pontos de luz do cmodo que voc deve informar so os pontos da luz geral e Lumos vai ajudar voc a identificar esses pontos!
                </p>

                <p className="pdl-text">
                    Os pontos de luz geral normalmente esto localizados no teto (laje ou forro). 
                    S no caso de no haver nenhum ponto no teto, observe se h pontos na parede (arandelas) e, somente neste caso, considere esses pontos. 
                    Em ambas as situaes, os pontos da luz geral so aqueles que ficam distribudos uniformemente. Isso porque a inteno  espalhar a luz por igual no ambiente. 
                </p>

                <p className="pdl-text">
                    A luz geral pode ser ofertada por um ou mais de um conjunto de equipamentos (lmpada e luminria), sempre distribudos uniformemente, por isso, quando  ofertada s por um conjunto de equipamento, esse fica localizado no centro do ambiente. 
                    Pode ser que seu cmodo tenha luz geral e luz de destaque no teto e voc precise identificar qual  a luz geral. 
                    Nesse caso, veja qual a luz que no est direcionada para algum mvel ou objeto de decorao, pois luz geral no  vinculada a disposio dos mobilirios no cmodo (layout), visto que ela  uma luz para o ambiente.
                </p>

                <p className="pdl-text">
                    Agora que voc j sabe reconhecer a luz geral, volte no Light Match e informe o n de pontos de luz do seu cmodo, mas antes veja essas recomendaes que colocamos aqui para voc sobre <span onClick={openPopupLuminarias}><b> luminrias </b></span>, <span onClick={openPopupSancas}><b> sancas de gesso </b></span>, e <span onClick={openPopupNaoId}><b> quando voc no conseguiu identificar seus pontos de luz geral </b></span>.
                </p>
            </div>

            <div className='popup' id='popup-luminarias'>
                <InformationPopup 
                    title={luminariasTitle}
                    text={luminariasText}
                    containerId="popup-luminarias"
                    blackoutId="popup-blackout"
                />
            </div>

            <div className='popup' id='popup-sancas'>
                <InformationPopup 
                    title={sancasTitle}
                    text={sancasText}
                    containerId="popup-sancas"
                    blackoutId="popup-blackout"
                />
            </div>

            <div className='popup' id='popup-nao-id'>
                <InformationPopup 
                    title={niTitle}
                    text={niText}
                    containerId="popup-nao-id"
                    blackoutId="popup-blackout"
                />
            </div>

            <div id='popup-blackout'></div>
        </div>
    );
};

export default PontosDeLuz;