import './AtitudesSustentaveis.css';
import { useNavigate } from "react-router-dom";
import leftArrowImage from "../../Assets/Images/leftArrow.svg";
import shareImage from "../../Assets/Images/share.svg";
import devmob from "../../Assets/Images/devmob.png";
import labca from "../../Assets/Images/labca.png";
import InformationPopup from '../InformationPopup/InformationPopup';
import { useRef } from "react";

const AtitudesSustentaveis = () => {
    const labcaTitle = "LabCA - Laboratrio de Anlise e Criao de Ambincias";
    const labcaText = "Grupo de pesquisa que investiga aspectos sensoriais do espao voltados para o estmulo da percepo tanto objetiva como subjetiva do usurio que constitui a inter-relao pessoa-ambiente e que colabora na construo do lugar fsico, sociolgico e fenomenolgico, promovendo a identidade espacial. Essas atividades se do de forma transdisciplinar na gerao de conhecimento, trabalhando a promoo do bem estar do ser humano atravs da ergonomia do mobilirio e dos equipamentos, da relao edifcio, cidade e paisagem e da cincia do conforto ambiental, no contexto da sustentabilidade, que envolvem as reas afins do Design, do Design de Interior, do Paisagismo, da Arquitetura e do Urbanismo. A partir da integrao entre ensino, pesquisa e extenso, o LABCA visa  repercusso de seus trabalhos nos meios de divulgao cientfica, com produo bibliogrfica, tcnica, artstica e cultural que envolva outras Instituies e a Sociedade.";
    const devmobTitle = "DevMob";
    const devmobText = "Grupo de Interesse e Extenso em Desenvolvimento de Aplicativos Mveis do Instituto de Computao (IC) daUFRJ- Universidade Federal do Rio de Janeiro.";
    
    const navigate = useNavigate();

    const sectionRef = useRef(null);

    const scrollToSection = () => {
        sectionRef.current.scrollIntoView({ behavior: "auto" });
    };

    const handleClose = () => {
        navigate('/home');
    };

    const openPopupLabca = () => {
        scrollToSection();
        document.getElementById('popup-labca').style.visibility = 'visible';
        document.getElementById('popup-blackout').style.visibility = 'visible';
    }

    const openPopupDevmob = () => {
        document.getElementById('popup-devmob').style.visibility = 'visible';
        document.getElementById('popup-blackout').style.visibility = 'visible';
    }

    return (
        <div className="atitudes-sustentaveis">
            <div className="atitudes-title-container">
                <div className="atitudes-title-buttons-container">
                    <div
                        onClick={handleClose} 
                        className="atitudes-button">
                        <img src={leftArrowImage} alt='Home'></img>
                    </div>

                    <div className="atitudes-button">
                        <img src={shareImage} alt='Compartilhar'></img>
                    </div>
                </div>

                <div className="atitudes-title">
                    <p className="atitudes-title-text">
                       Atitudes Sustentveis
                    </p>
                    <p className="atitudes-title-subtext">
                       LabCa (UFRJ)
                    </p>
                </div>
            </div>

            <div className="atitudes-text-container">
                <p className="atitudes-text">
                    O projeto Atitudes Sustentveis  um projeto de extenso da Universidade Federal do Rio de Janeiro que realiza diversas aes dedicadas  educao e popularizao da cincia e da tecnologia com o objetivo de promover a insero e a difuso no mercado de produtos e processos de inovao tecnolgica, estimulando mudanas no padro tanto de consumo da populao, como de produo de bens e servios ofertados atualmente.
                </p>

                <p className="atitudes-text">
                    A partir de demandas da sociedade identificadas pelos resultados das pesquisas realizadas no <span onClick={openPopupLabca}><b> LabCA  - Laboratrio de Anlise e Criao de Ambincias </b></span> da Escola de Belas Artes - EBA/UFRJ - com o qual o projeto Atitudes Sustentveis tem vnculo, dentro da interao dialgica entre ensino pesquisa e extenso, algumas dessas aes se voltam para as novas tecnologias de lmpadas de alto rendimento luminoso, usadas no espao interno residencial em substituio s lmpadas incandescentes.
                </p>

                <p className="atitudes-text" ref={sectionRef}>
                    O aplicativo Lumos  uma iniciativa do projeto Atitudes Sustentveis viabilizada pelo projeto de extenso <span onClick={openPopupDevmob}><b> PROMOVE </b></span> do Instituto de Computao (IC) da UFRJ. Essa parceria entre esses dois projetos de extenso da mesma Instituio Federal de Ensino Superior est alinhada com o ODS - Objetivo de Desenvolvimento Sustentvel - 12 (selo ODS 12) que consiste em assegurar padres de produo e de consumo sustentveis, presente na Agenda 2030 da Organizao das Naes Unidas, com a qual o Brasil est comprometido, e atende a poltica da Universidade de promover sustentabilidade atravs da educao.
                </p>
            </div>

            <div className='popup' id='popup-labca'>
                <InformationPopup 
                    title={labcaTitle}
                    text={labcaText}
                    containerId="popup-labca"
                    blackoutId="popup-blackout"
                    bottomImg={labca}
                    bottomImgSize="16rem"
                />
            </div>

            <div className='popup' id='popup-devmob'>
                <InformationPopup 
                    title={devmobTitle}
                    text={devmobText}
                    containerId="popup-devmob"
                    blackoutId="popup-blackout"
                    bottomImg={devmob}
                    bottomImgSize="15rem"
                />
            </div>

            <div id='popup-blackout'></div>
        </div>
    );
};

export default AtitudesSustentaveis;